import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    font-size: 1.1rem;
`;

class Clock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            time: new Date()
        };
    }
    componentDidMount() {
        this.intervalID = setInterval(() => this.tick(), 1000);
    }
    componentWillUnmount() {
        clearInterval(this.intervalID);
    }
    tick() {
        this.setState({
            time: new Date()
        });
    }

    render() {
        const {time} = this.state;

        const options = {
            year: 'numeric',
            month: '2-digit',
            day: '2-digit',
            hour: '2-digit',
            minute: '2-digit',
            second: '2-digit'
        };
        const easternOptions = options;

        const local = new Intl.DateTimeFormat('de-DE', options).format(time);
        easternOptions.timeZone = 'America/New_York';
        const eastern = new Intl.DateTimeFormat('de-DE', easternOptions).format(time);

        return (
            <Container>
                <div>Locale: {local}</div>
                <div>Eastern: {eastern}</div>
            </Container>
        );
    }
}
export default Clock;
